/* eslint new-cap: 0 */

import React from 'react';
import { Route } from "react-router-dom";

/* containers */
import { App } from './containers/App';

import { checkAuth, requireAuth } from './components/DetermineAuth';
import * as pages from './pages/index';

const Foo = () => <h1>Foo</h1>;
const Bar = () => <h1>Bar</h1>;

const authOr404 = (Component) => requireAuth(Component, <NotFound />);

export default (
  <Route path="/" component={App}>
    <Route path="login" component={checkAuth(pages.Login)} />

    <Route path="foo" component={checkAuth(Foo)} />

    <Route path="bar" component={authOr404(Bar)} />

    <Route path="*" component={checkAuth(pages.NotFound)} />
  </Route>
);
