import React from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { Redirect } from 'react-router-dom';
import * as actionCreators from '../actions/auth';

function mapStateToProps(state) {
  return {
    token: state.auth.token,
    authTime: state.auth.authTime,
  };
}

function mapDispatchToProps(dispatch) {
  return bindActionCreators(actionCreators, dispatch);
}

const TOKEN_EXPIRATION = 5; // hours

function expiredAuthentication(authTime) {
  // return true if authTime < (now-expiration time)
  return authTime.isBefore(moment().subtract(TOKEN_EXPIRATION, 'hours'))
}

const DetermineAuth = ({token, authTime, whenUnauthorized, children}) => {
  const finalToken = token || localStorage.getItem('token') || '';
  if (token == null) {
    if (finalToken != '') {
      validateToken(finalToken);
    }
  } else if (expiredAuthentication(authTime)) {
    validateToken(finalToken);
  }
  if (finalToken === '' && redirect != null) {
    return whenUnauthorized;
  } else {
    return children;
  }
};

const ConnectedDetermineAuth = connect(mapStateToProps, mapDispatchToProps)(DetermineAuth);

export function requireAuth(Component, whenUnauthorized) {
  return <ConnectedDetermineAuth whenUnauthorized={whenUnauthorized}>{ Component }</ConnectedDetermineAuth>
}

export function checkAuth(Component) {
  return <ConnectedDetermineAuth>{ Component }</ConnectedDetermineAuth>;
}